const StyledSocialLinks = styled.div`
  a {
    opacity: 0.5;
    margin: 0 1rem;
    color: ${({ theme }) => (theme.color)};
    &:hover {
      opacity: 1;
      color: ${({ theme }) => (theme.color)};
    }
  }
`;

export default function SocialLinks() {
  return (
    socialData.map(function ({
      id,
      name,
      icon,
      url,
    }) {
      return (
        <StyledSocialLinks id={id}>
          <a
            href={url}
            aria-label={name}
            className="link-icons"
            target="_blank"
            rel="noreferrer"
            >
            <Icon icon={icon}/>
          </a>
      </StyledSocialLinks>
      );
    })
  );
}
